@font-face {
  font-family: "Open Sans";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("..fonts/opensans.woff") format "woff", url("..fonts/opensans.woff2") format "woff2";
}
@font-face {
  font-family: "Open Sans";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("..fonts/opensansbold.woff") format "woff", url("..fonts/opensansbold.woff2") format "woff2";
}
.about-us {
  min-width: 260px;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .about-us {
    padding: 50px;
    min-width: 768px;
    padding-top: 59px;
    padding-bottom: 90px;
  }
}
@media (min-width: 1150px) {
  .about-us {
    padding: 0;
    padding-top: 68px;
    padding-bottom: 98px;
  }
}
.about-us__wrapper {
  padding: 30px;
  display: flex;
  flex-direction: column;
}

@media (min-width: 1150px) {
  .about-us__wrapper {
    padding-left: 40px;
    padding-right: 40px;
  }
}
.about-us__title {
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  align-items: center;
  margin: 0;
  margin-bottom: 28px;
}

@media (min-width: 768px) {
  .about-us__title {
    font-size: 43px;
    line-height: 50px;
    margin-bottom: 41px;
  }
}
@media (min-width: 1150px) {
  .about-us__title {
    font-size: 53px;
    line-height: 57px;
    margin-bottom: 39px;
  }
}
.about-us__list {
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-rows: auto;
  row-gap: 32px;
}

@media (min-width: 768px) {
  .about-us__list {
    grid-template-columns: minmax(260px, 265px) minmax(320px, 325px);
    grid-template-rows: auto;
    column-gap: 70px;
    row-gap: 55px;
    font-size: 20px;
    line-height: 22px;
  }
}
.about-us__item {
  display: flex;
  flex-direction: column;
}

.about-us__item:nth-child(1) .about-us__item-icon {
  margin-bottom: 16px;
}

.about-us__item:nth-child(2) .about-us__item-icon {
  margin-bottom: 26px;
}

.about-us__item:nth-child(3) .about-us__item-icon {
  margin-bottom: 21px;
}

.about-us__item:nth-child(4) .about-us__item-icon {
  margin-bottom: 17px;
}

.about-us__item:nth-child(5) .about-us__item-icon {
  margin-bottom: 26px;
}

.about-us__item:nth-child(6) .about-us__item-icon {
  margin-bottom: 20px;
}

@media (min-width: 768px) {
  .about-us__item:nth-child(1),
.about-us__item:nth-child(2),
.about-us__item:nth-child(3) {
    grid-column: 1/2;
  }

  .about-us__item:nth-child(4),
.about-us__item:nth-child(5),
.about-us__item:nth-child(6) {
    grid-column: 2/3;
  }

  .about-us__item:nth-child(1),
.about-us__item:nth-child(4) {
    grid-row: 1/2;
  }

  .about-us__item:nth-child(2),
.about-us__item:nth-child(5) {
    grid-row: 2/3;
  }

  .about-us__item:nth-child(3),
.about-us__item:nth-child(6) {
    grid-row: 3/4;
  }
}
.additional-list {
  display: none;
}

.additional-list__item:hover {
  opacity: 0.6;
}

.additional-list__item:active {
  opacity: 0.3;
}

@media (min-width: 1150px) {
  .additional-list {
    display: flex;
    flex-wrap: wrap;
    font-weight: 700;
    font-size: 15px;
    line-height: 24px;
    margin: 0;
    margin-top: 19px;
    padding: 0;
    padding-left: 50px;
    order: 2;
    max-width: 600px;
  }

  .additional-list__item {
    margin-bottom: 19px;
  }

  .additional-list__link {
    color: black;
    text-decoration: none;
    padding-top: 19px;
    padding-bottom: 19px;
    padding-right: 55px;
  }
}
.button {
  display: block;
  height: 52px;
  font-size: 18px;
  font-weight: 700;
  line-height: 20px;
  text-transform: uppercase;
  text-decoration: none;
  border: 2px solid #444444;
  cursor: pointer;
  color: #444444;
  background: white;
  text-align: center;
  box-sizing: border-box;
}

.button:hover {
  color: white;
  background: #444444;
}

.button:active {
  color: white;
  background: #444444;
  opacity: 0.3;
}

.catalog-container {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  background-color: #ffffff;
}

.catalog-filter {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}

.catalog-filter__fieldset {
  border: none;
  margin: 0 auto;
  max-width: 1050px;
  padding-left: 30px;
  padding-right: 30px;
}

.catalog-filter__title {
  font-size: 17px;
  line-height: 16px;
  font-weight: 700;
  color: #63d1bb;
  margin: 0;
  padding: 0;
  text-transform: uppercase;
}

.catalog-filter__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.catalog-filter__control-label {
  font-weight: 700;
  font-size: 17px;
  line-height: 18px;
}

.catalog-filter__require {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #444444;
}

.catalog-filter__label {
  font-weight: 700;
  font-size: 17px;
  line-height: 18px;
}

.catalog-filter__button {
  font-size: 17px;
  line-height: 16px;
  text-transform: uppercase;
  color: #454545;
}

.catalog-filter__inner {
  display: grid;
  grid-template-columns: 1fr;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .catalog-filter__wrapper:nth-child(2n) {
    background-color: #f2f2f2;
  }

  .catalog-filter__fieldset {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    padding-left: 50px;
    padding-right: 50px;
  }

  .catalog-filter__title {
    grid-column: 1/2;
  }

  .catalog-filter__list {
    grid-column: 2/4;
  }

  .catalog-filter__color:nth-child(2n+1) {
    grid-column: 3/4;
  }

  .catalog-filter__color:nth-child(2n) {
    grid-column: 2/3;
  }

  .catalog-filter__name {
    grid-column: 2/-1;
  }

  .catalog-filter__surname {
    grid-column: 2/-1;
  }

  .catalog-filter__patronymic {
    grid-column: 2/-1;
  }

  .catalog-filter__phone-number {
    grid-column: 2/4;
  }

  .catalog-filter__email-text {
    grid-column: 2/4;
  }

  .catalog-filter__message-text {
    grid-column: 2/4;
  }

  .catalog-filter__inner {
    grid-template-columns: 1fr 1fr;
    padding-left: 50px;
    padding-right: 50px;
  }

  .catalog-filter__require {
    grid-column: 2/3;
    text-align: end;
  }

  .catalog-filter__button {
    grid-column: 1/2;
  }
}
@media (min-width: 1150px) {
  .catalog-filter__fieldset {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }

  .catalog-filter__list {
    grid-column: 2/-1;
  }

  .catalog-filter__color:nth-child(3n+1) {
    grid-column: 4/5;
  }

  .catalog-filter__color:nth-child(3n) {
    grid-column: 3/4;
  }

  .catalog-filter__color:nth-child(3n+2) {
    grid-column: 2/3;
  }

  .catalog-filter__phone-number {
    grid-column: 2/-1;
  }

  .catalog-filter__email-text {
    grid-column: 2/-1;
  }

  .catalog-filter__message-text {
    grid-column: 2/-1;
  }

  .catalog-filter__inner {
    min-width: 1050px;
  }
}
.catalog {
  position: relative;
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
}

.catalog__inner {
  background-color: #f9f9f9;
}

.catalog__wrapper {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  background-image: url("../../../source/img/background-mobile.jpg");
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: 50% -17px;
  max-width: 1150px;
}

@media (min-width: 768px) {
  .catalog__wrapper {
    background-position: 50% -60px;
    background-image: url("../../../source/img/background-tablet.jpg");
  }

  .catalog__wrapper::after {
    position: absolute;
    content: "";
    bottom: 0;
    left: 0;
    width: 100%;
    height: 24px;
    background-image: url("data:image/svg+xml;base64,");
  }
}
@media (min-width: 1150px) {
  .catalog__wrapper {
    background-position: 50% 82px;
    background-image: url("../../img/background-desktop.jpg");
  }

  .catalog__wrapper::after {
    content: "";
    bottom: 0;
    left: 0;
    width: 100%;
    height: 24px;
    background-image: url("data:image/svg+xml;base64,");
    max-width: 1150px;
  }
}
.catalog__title {
  flex-grow: 1;
  font-weight: 700;
  font-size: 27px;
  line-height: 30px;
  text-align: center;
  margin: 0 auto;
  padding-top: 73px;
  padding-bottom: 96px;
  padding-left: 30px;
  padding-right: 30px;
  min-height: 114px;
  max-width: 700px;
}

@media (min-width: 768px) {
  .catalog__title {
    padding: 104px 50px 358px 50px;
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .catalog__title {
    padding: 95px 50px 356px 50px;
    font-size: 53px;
    line-height: 57px;
  }
}
.catalog__decor {
  background-color: #63d1bb;
  position: relative;
  flex-grow: 1;
}

.catalog__decor:hover {
  background-color: #59d5bc;
}

.catalog__decor-icon {
  color: #149080;
  opacity: 0.5;
}

.catalog__decor-icon:hover {
  opacity: 0.7;
}

.catalog__decor-icon:active {
  opacity: 0.3;
}

.catalog__toy {
  background-color: #56c3b2;
  flex-grow: 1;
  position: relative;
}

.catalog__toy-icon {
  color: #149080;
  opacity: 0.5;
}

.catalog__toy-icon:hover {
  opacity: 0.7;
}

.catalog__toy-icon:active {
  opacity: 0.3;
}

.catalog__toy:hover {
  background-color: #47c2af;
}

.catalog__decor:hover,
.catalog__toy:hover {
  transform: translateY(-5px);
}

.catalog__link:hover {
  transform: translateY(-5px);
}

.catalog__decor:active,
.catalog__toy:active {
  transform: translateY(-5px);
}

.catalog__link:active {
  transform: translateY(-5px);
}

.catalog__link {
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  text-align: start;
  color: white;
  text-decoration: none;
  display: flex;
  justify-content: space-between;
  padding: 23px 36px 18px 30px;
}

.catalog__toy .catalog__link {
  padding: 21px 27px 22px 30px;
}

.catalog__link-inner {
  max-width: 130px;
}

@media (min-width: 768px) {
  .catalog__link {
    font-size: 20px;
    line-height: 30px;
    padding-bottom: 42px;
  }

  .catalog__decor,
.catalog__toy {
    min-width: 334px;
  }

  .catalog__decor {
    margin-left: 50px;
  }

  .catalog__toy {
    margin-right: 50px;
  }
}
@media (min-width: 1150px) {
  .catalog__link {
    padding: 25px 36px 34px 37px;
    font-size: 20px;
    line-height: 30px;
  }

  .catalog__toy .catalog__link {
    padding: 25px 35px 34px 36px;
  }
}
.catalog__card-inner {
  margin: 0 auto;
  max-width: 1050px;
}

.catalog__card {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 30px;
  padding-bottom: 36px;
}

.catalog__card-title {
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  align-items: center;
  margin: 0;
  margin-bottom: 4px;
}

.catalog__card::after {
  content: "";
  width: 100%;
  height: 6px;
  position: absolute;
  bottom: 0;
  left: 0;
  background-image: url("data:image/svg+xml;base64,");
}

.catalog__card-best {
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  color: #63d1bb;
  align-items: center;
  text-transform: uppercase;
  margin-bottom: 17.5px;
}

.catalog__property {
  margin-bottom: 26px;
}

.catalog__property-inner {
  display: flex;
  border-bottom: solid 1px #ededed;
  padding-bottom: 12px;
}

.catalog__property-inner:not(:first-child) {
  margin-top: 8px;
}

.catalog__card-property {
  min-width: 97px;
}

.catalog__card-property,
.catalog__card-property-value {
  font-weight: 400;
  font-size: 15px;
  line-height: 20px;
  color: #444444;
  align-items: center;
}

.catalog__card-image {
  margin-bottom: 25.5px;
}

.catalog__card-price {
  color: #444444;
  text-align: center;
  font-size: 17px;
  line-height: 24px;
  margin-bottom: 24.5px;
}

.catalog__card-description {
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  align-items: center;
  color: #444444;
  max-width: 292px;
  margin: 0;
  margin-bottom: 28px;
  padding: 0;
}

.catalog__card-button {
  max-width: 260px;
  padding: 16px 70px 16px 70px;
}

@media (min-width: 768px) {
  .catalog__card {
    display: grid;
    grid-template-columns: minmax(292px, 293px) 1fr;
    grid-template-rows: auto;
    column-gap: 42px;
    padding: 50px;
    padding-top: 44px;
    padding-bottom: 58px;
    margin: 0;
  }

  .catalog__card::after {
    height: 17px;
    background-image: url("data:image/svg+xml;base64,");
  }

  .catalog__card-title {
    grid-column: 1/3;
    grid-row: 1/2;
    padding-bottom: 37.5px;
    margin: 0;
    font-size: 43px;
    line-height: 50px;
  }

  .catalog__card-image {
    position: absolute;
    width: 334px;
    height: 404px;
    right: 50px;
    top: 142px;
  }

  .catalog__card-best {
    grid-column: 2/3;
    grid-row: 1/2;
    text-align: end;
    line-height: 17px;
  }

  .catalog__card-best-inner {
    display: inline-block;
    text-align: center;
    max-width: 100px;
  }

  .catalog__card-description {
    grid-column: 1/2;
    grid-row: 2/3;
    font-size: 17px;
    line-height: 30px;
    margin-bottom: 48px;
  }

  .catalog__property {
    grid-column: 1/2;
    grid-row: 3/4;
    margin-bottom: 53px;
    column-gap: 2px;
  }

  .catalog__card-price {
    grid-column: 1/2;
    grid-row: 4/5;
    text-align: center;
    margin-bottom: 33px;
    font-size: 20px;
    line-height: 24px;
  }

  .catalog__card-button {
    grid-column: 1/2;
    grid-row: 5/6;
    max-width: 292px;
    padding: 16px 86px;
  }
}
@media (min-width: 1150px) {
  .catalog__card {
    display: grid;
    grid-template-columns: 0.3fr 1.2fr 2fr;
    padding-top: 129px;
    padding-bottom: 87px;
    column-gap: 50px;
  }

  .catalog__card-title {
    font-size: 53px;
    line-height: 57px;
    grid-column: 2/4;
    grid-row: 1/2;
    z-index: 1;
  }

  .catalog__card::after {
    height: 23px;
    width: 1050px;
    left: 0;
    background-image: url("data:image/svg+xml;base64,");
  }

  .catalog__card-image {
    position: absolute;
    width: 525px;
    height: 636px;
    right: 0;
    top: 66px;
  }

  .catalog__card-best {
    grid-column: 1/2;
    grid-row: 1/2;
    text-align: start;
  }

  .catalog__card-description {
    grid-column: 2/3;
    grid-row: 2/3;
    margin-bottom: 60px;
  }

  .catalog__property {
    grid-column: 2/3;
    grid-row: 3/4;
    margin-bottom: 106px;
  }

  .catalog__card-price {
    grid-column: 2/3;
    grid-row: 4/5;
    margin-bottom: 33px;
  }

  .catalog__card-button {
    grid-column: 2/3;
    grid-row: 5/6;
  }
}
