    max-width: 200px;
    margin-top: 32.5px;
    margin-bottom: 31.5px;
    margin-right: 0;
  }

  .site-list__link {
    padding-top: 32.5px;
    padding-bottom: 34px;
    font-size: 17px;
    line-height: 24px;
  }

  .site-list__item:nth-child(2n+1) .site-list__link {
    padding-right: 35px;
  }

  .site-list__item:nth-child(2n+1) .site-list__link::after {
    position: absolute;
    content: "";
    height: 100%;
    width: 1px;
    background-color: rgba(0, 0, 0, 0.1);
    right: 0;
    top: 0;
  }

  .site-list__item:nth-child(2n) .site-list__link {
    padding-left: 35px;
  }
}
